import { j as json } from "../../../../chunks/index.js";
import { g as getAllUsers } from "../../../../chunks/user.js";
async function GET() {
  try {
    const [users] = await Promise.all([getAllUsers()]);
    const usersById = users.reduce((obj, user) => {
      obj[user.id] = user;
      return obj;
    }, {});
    return json({
      status: "success",
      usersById
    });
  } catch (error) {
    console.error(error);
    return json({ status: "error", error });
  }
}
export {
  GET
};
